body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  background-color: #429bb3 !important;
}

.card {
  max-width: 500px;
  padding: 10px 30px;
  background-color: rgba(255,255,255,.1);
  border-radius: 3px;
}
.card .image {
  width: calc(100% - 40px);
  height: 300px;
  padding: 1px 20px;
  background-size: cover;
  border-radius: 3px;
}

.card .address {
  height: 3em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.card .price {
  font-weight: normal;
}

h3, h4, h5, .image {
  margin: 20px 0;
}
.label {
  font-weight: normal;
  opacity: .7;
}
